import Web3 from 'web3';
import TriangleABI from './abi/triangleABI.json'

let isTestnet = false

// main net
let addressTriangle = "0x6EA59DBbc9fbB8E57e985ECdE702e9f0BfFC116C"

let providerTemp = new Web3.providers.HttpProvider("https://api.avax.network/ext/bc/C/rpc"); 

// testnet 
if(isTestnet){

    addressTriangle = ""
    providerTemp = new Web3.providers.HttpProvider("https://api.avax-test.network/ext/bc/C/rpc"); 
}

export  const provider = providerTemp
export const addressExportTriangle = addressTriangle

export const getWeb3 = () =>
    new Promise(async(resolve, reject) => {
    if (window.ethereum) {
        const web3 = new Web3(window.ethereum);
        try {
            // await window.ethereum.enable();
            const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
            resolve(web3);
        } catch (error) {
        reject(error);
        }
    }
    else {
 
        const web3 = new Web3(provider);
        console.log("No web3 instance injected, using Local web3.");
        resolve(web3);
    }
});

export const getUser = async () => {
    if (window.ethereum) {
        let web3 =   new Web3(window.ethereum);

         window.user = (await web3.eth.getAccounts())[0];

    }
    return window.user
}

export const getInstanceTriangle = async (web3) => {
    window.user = await getUser();

    window.instanceTriangle= new web3.eth.Contract(
        TriangleABI,
        addressTriangle,
        {
            from: window.user
        }
    );
    return window.instanceTriangle;
}

