import React, { useState, useEffect } from 'react';
import { provider, getInstanceTriangle, addressExportTriangle } from '../Web3Util'

import Header from '../partials/Header';
import Footer from '../partials/Footer';
import Web3 from 'web3';

import triangleLogo from '../images/logo.png'
import triangleStake from '../images/stake.png'
import axios from 'axios';

function StakePage() {
  let hasEthereum = window.ethereum != undefined
  const [contractInstanceTriangle, contractUpdaterTriangle] = useState(null)
  const [userNodeItems, userNodeItemsUpdater] = useState([])
  const [triangleToken, triangleTokenUpdater] = useState(0)
  const [selectedGasTokenAmount, selectedGasTokenAmountUpdater] = useState(0)
  const [selectedStakeDuration, selectedStakeDurationUpdater] = useState(0)
  const [totalBonus, totalBonusUpdater] = useState(0)
  const [activeStakes, activeStakesUpdater] = useState(0)
  const [userAllowance, userAllowanceUpdater] = useState(0)
  const [sumToClaim, sumToClaimUpdater] = useState(0)

  const [isClaiming, isClaimingUpdater] = useState(false)
  
  const [trianglePriceInGasToken, trianglePriceInGasTokenUpdater] = useState(0)
  const [gasTokenPrice, gasTokenPriceUpdater] = useState(0)



  useEffect(async () => {

    if (contractInstanceTriangle == null) {
      let contractInstanceTriangleNew

      let web3
      if (window.user != undefined) {
        web3 = new Web3(window.ethereum);
      } else {
        web3 = new Web3(provider);
      }
      contractInstanceTriangleNew = await getInstanceTriangle(web3)

      await contractUpdaterTriangle(contractInstanceTriangleNew)

      await loadData(contractInstanceTriangleNew)
    }
  });

  function onChangeGasToken(event) {
    selectedGasTokenAmountUpdater(event.target.value)
  }

  function onChangeStakeDuration(event) {
    selectedStakeDurationUpdater(event.target.value)
  }


  async function createStake() {
    if (selectedGasTokenAmount <= 0) {
      alert("must send a value greater than 0")
      return
    }
    else if (selectedStakeDuration <= 0) {
      alert("must stake for at least 1 second")
      return
    }
    isClaimingUpdater(true)
    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.startStake(selectedStakeDuration,Web3.utils.toWei(selectedGasTokenAmount)).send()
    } catch (e) {
      console.log("@@@ e", e)
    }finally{
      isClaimingUpdater(false)
    }
  }

  async function claimRewards(nodeId, showPrompt) {

    if(showPrompt){
      let confirmation = window.confirm("Attention. You're about to end your stake prematurely. Are you sure?");

      if (!confirmation) {
        return
      }
    }
    

    isClaimingUpdater(true)

    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.claimRewardsForStake(nodeId).send()
    } catch (e) {
    }finally{
      isClaimingUpdater(false)
    }
  }

  function timeout(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  async function sleep(fn, ...args) {
    await timeout(1000);
    return fn(...args);
  }

  async function loadData(contractInstanceTriangle) {
    if (window.user != null) {
      let web3 = new Web3(window.ethereum);
      let userNodesCallArray = []
      let stakesBonusCallsArray = []
      let stakesRewardsCallsArray = []
      let localPrice =0

      const [userCurrencyBalance, activeStakes, localUserStakeCounter, totalBonus,trianglePriceInGasToken] = await Promise.all([
        contractInstanceTriangle.methods.balanceOf(window.user).call(),
        contractInstanceTriangle.methods.activeStakes().call(),
        contractInstanceTriangle.methods.userStakeCounter(window.user).call(),
        contractInstanceTriangle.methods.totalInvestedStake().call(),
        contractInstanceTriangle.methods.getOneTrianglePrice().call(),
      ])

      try{
        if(gasTokenPrice == 0){
          const gasTokenPrice = await  axios.get('https://api.coingecko.com/api/v3/simple/price?ids=avalanche-2&vs_currencies=usd',{ crossdomain: true })
          localPrice = gasTokenPrice.data["avalanche-2"].usd
          gasTokenPriceUpdater(parseFloat(localPrice))
        }
      }catch(e){
        localPrice = 0
        gasTokenPriceUpdater(0)
      }
      let localTokenPrice = parseFloat(Web3.utils.fromWei(trianglePriceInGasToken + "", 'ether'))
      trianglePriceInGasTokenUpdater(parseFloat(Web3.utils.fromWei(trianglePriceInGasToken + "", 'ether')))


      for (let counter = 0; counter < localUserStakeCounter; ++counter) {
        userNodesCallArray.push(contractInstanceTriangle.methods.addressToStakes(window.user, counter).call())
      }

      const localUserStakes = await Promise.all(userNodesCallArray)

      for (let counter = 0; counter < localUserStakes.length; ++counter) {
        stakesBonusCallsArray.push(contractInstanceTriangle.methods.calculateBonus(localUserStakes[counter].id).call())
        stakesRewardsCallsArray.push(contractInstanceTriangle.methods.calculateStakeRewardsForClaim(localUserStakes[counter].id).call())
      }

      const stakesBonus = await Promise.all(stakesBonusCallsArray)
      const stakesRewards = await Promise.all(stakesRewardsCallsArray)

      let localUserStakeItems = []
      let now = Date.now()
      now = now / 1000
      // console.log("@@@ now",now)
      let localSumToClaim =0
      for (let counter = 0; counter < localUserStakes.length; ++counter) {
        if (localUserStakes[counter].owner == window.user && localUserStakes[counter].isStaking) {
          let timeLeft = (Number.parseInt(localUserStakes[counter].stakeDuration) + Number.parseInt(localUserStakes[counter].stakeStart)) - now
          localSumToClaim = parseFloat(localSumToClaim) + parseFloat(Web3.utils.fromWei(stakesBonus[counter] + "", 'ether')) + parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether'))
          let localClaim = Number.parseFloat(Web3.utils.fromWei(stakesBonus[counter] + "", 'ether')) + Number.parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether'))
          localUserStakeItems.push(
            <div key={localUserStakes[counter].id} className="relative flex flex-col items-center p-6 dropblur infoDiv stake" data-aos="zoom-y-out" data-aos-delay="150">
              <div style={{ width: "100%" }}>
              <img src={triangleStake} style={{height:"55px",position:"absolute",top:20,right:25}}/>

                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" ,width:"100%"}}>Identifier  <br /><span style={{fontWeight:500}}>{localUserStakes[counter].id}</span></h4> <br />
                {/* <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left", width:"100%" }}>Stake weight  <br /><span style={{fontWeight:500}}>{Number.parseFloat(Web3.utils.fromWei(localUserStakes[counter].stakeWeight + "", 'ether'))} </span>
               </h4> <br /> */}
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Triangles in stake  <br /><span style={{fontWeight:500}}>{Number.parseFloat(Web3.utils.fromWei(localUserStakes[counter].baseInvested + "", 'ether'))}
                <img className='triangleLogo small' src={triangleLogo} />
                <br />
                  $ &nbsp;{parseFloat(parseFloat(Web3.utils.fromWei(localUserStakes[counter].baseInvested  + "", 'ether'))*parseFloat(localPrice/localTokenPrice/2 )).toFixed(6)}
                </span>
                 </h4> <br />

                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Stake bonus  <br /><span style={{fontWeight:500}}>{Number.parseFloat(Web3.utils.fromWei(stakesBonus[counter] + "", 'ether')).toFixed(18)}<img className='triangleLogo small' src={triangleLogo} />
                <br />
                  $ &nbsp;{parseFloat(parseFloat(Web3.utils.fromWei(stakesBonus[counter]  + "", 'ether'))*parseFloat(localPrice/localTokenPrice/2 )).toFixed(6)}
                </span></h4> <br />
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Stake rewards  <br /><span style={{fontWeight:500}}>{Number.parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether')).toFixed(18)}<img className='triangleLogo small' src={triangleLogo} />
                <br />
                  $ &nbsp;{parseFloat(parseFloat(Web3.utils.fromWei(stakesRewards[counter]  + "", 'ether'))*parseFloat(localPrice/localTokenPrice/2 )).toFixed(6)}
                </span></h4> <br />

                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Total to claim  <br /><span style={{fontWeight:500}}>
                  {localClaim} <img className='triangleLogo small' src={triangleLogo} />
                <br />
                  $ &nbsp;{parseFloat(parseFloat(localClaim)*parseFloat(localPrice/localTokenPrice/2 )).toFixed(6)}
                </span></h4> <br />

                {/* <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Stake start - {localUserStakes[counter].stakeStart}</h4> <br />
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Stake duration - {localUserStakes[counter].stakeDuration}</h4> <br /> <br /> <br />
                 */}
                {Number.parseInt(timeLeft) > 0 ?
                  <>
                  
                  <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left",marginBottom:"25px" }}>Seconds until stake ends <br /> <span style={{fontWeight:500}}>{Number.parseInt(timeLeft)} seconds</span></h4>
                  <br /> <br />
                  <br /> <br />
                  <br /> <br />
                  <button  className="button-64"  onClick={() => claimRewards(localUserStakes[counter].id,true)}>
                <span className="text">Claim rewards and <br/>end stake prematurely</span>
                </button>
                  </>
                  :
                  <>
                  <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left",marginBottom:"25px" }}>Seconds until stake ends  <br /><span style={{fontWeight:500}}>Stake ended</span></h4>
                  <br /> <br />
                  <br /> <br />
                  <br /> <br />
                  <button  className="button-64"  onClick={() => claimRewards(localUserStakes[counter].id,false)}>
                <span className="text">Claim rewards and <br/>close this window</span>
                </button>
                  </>
                }
               
              </div>
            </div>
          )
        }
      }
      sumToClaimUpdater(localSumToClaim)
      userNodeItemsUpdater(localUserStakeItems)
      triangleTokenUpdater(userCurrencyBalance)
      activeStakesUpdater(activeStakes)
      totalBonusUpdater(totalBonus)

    }

    await timeout(1000)
    await loadData(contractInstanceTriangle)

  }


  return (
    <div className="flex flex-col min-h-screen overflow-hidden">
  <div className="overlay"></div>
<div className="dynamicbg" style={{height:"100vh"}}>
{isClaiming?
<svg  viewBox="0 0 100 115" preserveAspectRatio="xMinYMin slice">
<polygon points="" fill="none" stroke="orange" strokeWidth="10.5">
      <animate attributeName="points" repeatCount="indefinite" dur="8s" begin="0s" from="50 57.5, 50 57.5, 50 57.5" to="50 -95, 175 100, -95 126"></animate>
    </polygon>

  </svg>
  :
  <svg viewBox="0 0 100 115" preserveAspectRatio="xMinYMin slice">
<polygon points="" fill="none" stroke="#3da5e3" strokeWidth="10.5">
      <animate attributeName="points" repeatCount="indefinite" dur="8s" begin="0s" from="50 57.5, 50 57.5, 50 57.5" to="50 -95, 175 100, -95 126"></animate>
    </polygon>

  </svg>
    }
 
</div>
      {/*  Site header */}
      <Header hasEthereum={hasEthereum} />

      {/*  Page content */}
      <main className="flex-grow">

        {/*  Page sections */}

        {/* <WalletBlocks bears={userNFTs}/> */}

        <section className="relative">
          {/* Section background (needs .relative class on parent and next sibling elements) */}
          <div className="absolute inset-0 top-1  md:mt-24 lg:mt-0 bg-gray-900 pointer-events-none" aria-hidden="true"></div>
          {/* <div className="absolute left-0 right-0 bottom-0 m-auto w-px p-px h-20 bg-gray-200 transform translate-y-1 "></div> */}

          <div className="relative max-w-6xl mx-auto px-4 sm:px-6">
            <div className="py-12 md:py-20">
            <br />
              <br />
              {/* Section header */}
              <br />
              <div className="max-w-2md mx-auto text-center  dropblur infoDiv stake">
              <br />

                <h2 className="h2 mb-4">Stake&nbsp;&nbsp;<img src={triangleStake} className='triangleLogo' style={{height:"65px",transform:"translatey(-10px)"}}/></h2>
                <p className="text-xl text-gray-600 showOnMobileBlock">Create and manage your stakes.</p>
                <p className="text-xl text-gray-600 hideOnMobileBlock">Create and manage your Triangle <img className='triangleLogo' src={triangleLogo} />  stakes.</p>
                <br />
                </div>
                <br /><br />
              {/* Items */}
              <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-4 items-start md:max-w-2xl lg:max-w-none">

                <p className="text-xl text-gray-600 dropblur infoDiv stake" style={{ textAlign: "center" }}><b>My Triangle balance</b> <br /><img className='triangleLogo' src={triangleLogo} /> {Number.parseFloat(Web3.utils.fromWei(triangleToken + "", "ether")).toFixed(15)}
                <br /> ${(parseFloat(Web3.utils.fromWei(triangleToken + "", "ether"))*parseFloat(gasTokenPrice/trianglePriceInGasToken/2 )).toFixed(2)}</p>
                <p className="text-xl text-gray-600 dropblur infoDiv stake" style={{ textAlign: "center" }}><b>Total active stakes</b> <br /><img src={triangleStake} style={{height:"25px",textAlign:"center",margin:"auto",marginBottom:"5px"}} />
                {activeStakes}
                </p>
                <p className="text-xl text-gray-600 dropblur infoDiv stake" style={{ textAlign: "center" }}><b>Total staked</b> <br /><img className='triangleLogo' src={triangleLogo} /> {Number.parseFloat(Web3.utils.fromWei(totalBonus + "", "ether"))}
                <br /> ${(parseFloat(parseFloat(Web3.utils.fromWei(totalBonus + "", "ether"))*gasTokenPrice/trianglePriceInGasToken/2 )).toFixed(2)}</p>
                <p className="text-xl text-gray-600 dropblur infoDiv stake" style={{ textAlign: "center" }}><b>Total to claim</b> <br /><img className='triangleLogo' src={triangleLogo} /> {sumToClaim.toFixed(15)}
                <br /> ${parseFloat(parseFloat(sumToClaim)*gasTokenPrice/trianglePriceInGasToken/2 ).toFixed(2)}</p>

              </div>
              <br />
              <br />
              <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-3 lg:grid-cols-3 items-start md:max-w-2xl lg:max-w-none">

                <p className="text-xl text-gray-600 dropblur infoDiv stake" style={{ textAlign: "center" }}>  
                  <input onChange={onChangeGasToken} style={{width:"100%",paddingTop:"13px",paddingBottom:"13px"}} placeholder='Amount in Triangle' type="number"  min={0} />
                </p>
                <p className="text-xl text-gray-600 dropblur infoDiv stake" style={{ textAlign: "center" }}>
                  <input onChange={onChangeStakeDuration} style={{width:"100%",paddingTop:"13px",paddingBottom:"13px"}} placeholder='Stake duration in seconds' min={0} max={157680000} type="number" />
                </p>
                <p className="text-xl text-gray-600 dropblur infoDiv stake" style={{ textAlign: "center" }}>
                  <button  className="button-64"  onClick={createStake}>
                  <span className="text">Create stake</span>
                  </button>
                </p>
              </div>

              <br />
              <br />

              <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-3 items-start md:max-w-2xl lg:max-w-none">

                {userNodeItems}

              </div>


            </div>
          </div>
        </section>

      </main>

      {/*  Site footer */}
      <Footer />

    </div>
  );
}

export default StakePage;