import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import {addressExportTriangle} from '../Web3Util'
import {getWeb3,getUser} from '../Web3Util'
import triangleLogo from '../images/logo.png'
import triangleNode from '../images/node.png'
import triangleStake from '../images/stake.png'

function Header(props) {

  const [top, setTop] = useState(true);
  const [currentUser, setUser] = useState(undefined);

  const [isMobileMenuOpen, setMobileMenuOpen] = useState(false);


  // detect whether user has scrolled the page down by 10px 
  useEffect(() => {
    const scrollHandler = () => {
      window.pageYOffset > 10 ? setTop(false) : setTop(true)
    };
    window.addEventListener('scroll', scrollHandler);
    return () => window.removeEventListener('scroll', scrollHandler);
  }, [top]);  

  useEffect(async ()=>{
    window.user = await getUser()
    setUser(window.user)
  })
  let hasEthereumAndNoUser =  props.hasEthereum && currentUser == undefined
  let hasEthereumAndUser = currentUser != undefined


  async function enableWeb() {
    if(hasEthereumAndNoUser){
      try{
        await getWeb3()
        window.location.reload()
      }catch(e){
  
      }
    }
  }

  return (
    <header className={`fixed w-full z-30 md:bg-opacity-90 transition duration-300 ease-in-out  ${!top && 'bg-white blur shadow-lg'}`}>
      <div className="max-w-6xl mx-auto px-5 sm:px-6">
        <div className="flex items-center justify-between h-16 md:h-20">
          {/* Site branding */}
          <div className="flex-shrink-0 mr-4">
            {/* Logo */}
            <Link to="/" className="block" aria-label="Cruip">
              <div className='header-title bg-clip-text text-transparent bg-gradient-to-r from-blue-500 to-teal-400'>
                {/* <img src={require("../images/logo.png").default} style={{height:"50px",float:"left", transform:"translatey(-2px)",marginRight:"15px"}} /> */}
                <img className='triangleLogo' src={triangleLogo} style={{height:"45px",float:"left", transform:"translatey(-2px)",marginRight:"-15px"}} />&nbsp;&nbsp;
                Triangle Finance
                </div>
            </Link>
          </div>
          {/* Site navigation */}
          <nav className="flex flex-grow hideOnMobile">
            <ul className="flex flex-grow justify-end flex-wrap items-center">

              {/* <li>
              <a  rel="noreferrer" href="https://traderjoexyz.com/#/trade?outputCurrency=0x0b5499556E7ddac6730eb9132336da392412b6b1" target="_blank" className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700">
                <span  className='cursor-pointer'>Get $TRIANGLE</span>
              </a>&nbsp;&nbsp;&nbsp;
            </li> */}
             
              {hasEthereumAndUser
                ?
                <>
                  <li>
                    <Link to="/node" className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700">
                      <span  className='cursor-pointer'><img className='triangleLogo' src={triangleNode} />&nbsp;&nbsp;Node</span>
                    </Link>&nbsp;&nbsp;&nbsp;
                  </li> 

                  <li>
                  <Link to="/stake" className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700">
                    <span  className='cursor-pointer'><img className='triangleLogo' src={triangleStake} />&nbsp;&nbsp;Stake</span>
                  </Link>
                  </li> 
                </>
              :
                <>
                  {hasEthereumAndNoUser
                    ?
                  <li>
                    <a  className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700 ">
                      <span onClick={()=>enableWeb()} className='cursor-pointer'>Connect </span>
                    </a>
                  </li>
                  :
                  <li>
                    <a  rel="noreferrer" href="https://metamask.io/download" target="_blank" className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700">
                      <span  className='cursor-pointer'>Metamask</span>
                    </a>
                  </li>
                  }
                </>
              }
               <li>
               &nbsp;&nbsp;&nbsp;<a  rel="noreferrer" href={`https://traderjoexyz.com/trade?outputCurrency=${addressExportTriangle}#/`} target="_blank" className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700">
                  <span  className='cursor-pointer'><img className='triangleLogo' src={triangleLogo} />&nbsp;&nbsp;On Traderjoe</span>
                </a>
              </li>
            </ul>
          </nav>

          <nav className="flex flex-grow showOnMobile" onClick={()=>setMobileMenuOpen(!isMobileMenuOpen)}>
            <ul className="flex flex-grow justify-end flex-wrap items-center">
               <li>
                <a className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700" style={{fontSize:"22px"}}>
                â˜°               
                </a>
              </li>
            </ul>
            {isMobileMenuOpen?

            <div style={{width:"100%",position:"absolute",left:0,top:"64px",padding:"20px"}} className={` md:bg-opacity-90 transition duration-300 ease-in-out ${!top && 'bg-white blur shadow-lg'}`}>
                
                <ul className="flex flex-grow justify-center flex-wrap items-center" >

                    <li style={{ width:"100%", textAlign:'center', marginBottom:"12px"}} >
                      <a  rel="noreferrer"  href={`https://traderjoexyz.com/trade?outputCurrency=${addressExportTriangle}#/`} target="_blank" className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700">
                        <span  className='cursor-pointer'><img className='triangleLogo' src={triangleLogo} />&nbsp;&nbsp;On Traderjoe</span>
                      </a>
                    </li>

                    {hasEthereumAndUser
                      ?
                      <>
                        <li style={{ width:"100%", textAlign:'center', marginBottom:"12px"}}>
                          <Link to="/node" className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700">
                            <span  className='cursor-pointer'><img className='triangleLogo' src={triangleNode} />&nbsp;&nbsp;Node</span>
                          </Link>
                        </li>
                      
                        <li style={{ width:"100%", textAlign:'center', marginBottom:"12px"}}>
                          <Link to="/stake" className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700">
                            <span  className='cursor-pointer'><img className='triangleLogo' src={triangleStake} />&nbsp;&nbsp;Stake</span>
                          </Link>
                        </li> 
                      </>
                    :
                      <>
                        {hasEthereumAndNoUser
                          ?
                        <li style={{ width:"100%", textAlign:'center', marginBottom:"12px"}}>
                          <a  className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700 ">
                            <span onClick={()=>enableWeb()} className='cursor-pointer'>Connect </span>
                          </a>
                        </li>
                        :
                        <li style={{ width:"100%", textAlign:'center', marginBottom:"12px"}}>
                          <a  rel="noreferrer" href="https://metamask.io/download" target="_blank" className="btn-sm text-gray-200 bg-gray-800 hover:bg-gray-700">
                            <span  className='cursor-pointer'>Metamask</span>
                          </a>
                        </li>
                        }
                      </>
                    }
                    </ul>

            </div>

            : null
            
            }
          </nav>   

         
        </div>
      </div>
    </header>
  );
}

export default Header;
