import React, { useState, useEffect } from 'react';
import { provider, getInstanceTriangle, addressExportTriangle } from '../Web3Util'
import axios from 'axios';
import Header from '../partials/Header';
import Footer from '../partials/Footer';
import Web3 from 'web3';
import triangleLogo from '../images/logo.png'
import triangleNode from '../images/node.png'

function NodePage() {
  let hasEthereum = window.ethereum != undefined
  const [contractInstanceTriangle, contractUpdaterTriangle] = useState(null)
  const [userNodeItems, userNodeItemsUpdater] = useState([])
  const [triangleToken, triangleTokenUpdater] = useState(0)
  const [selectedGasTokenAmount, selectedGasTokenAmountUpdater] = useState(0)
  const [gasTokenBalance, gasTokenBalanceUpdater] = useState(0)
  const [activeStakes, activeStakesUpdater] = useState(0)
  const [totalRewards, totalRewardsUpdater] = useState(0)
  const [dailyRewards, dailyRewardsUpdater] = useState(0)

  const [trianglePriceInGasToken, trianglePriceInGasTokenUpdater] = useState(0)
  const [gasTokenPrice, gasTokenPriceUpdater] = useState(0)

  const [isClaiming, isClaimingUpdater] = useState(false)



  useEffect(async () => {

    if (contractInstanceTriangle == null) {
      let contractInstanceTriangleNew

      let web3
      if (window.user != undefined) {
        web3 = new Web3(window.ethereum);
      } else {
        web3 = new Web3(provider);
      }
      contractInstanceTriangleNew = await getInstanceTriangle(web3)

      await contractUpdaterTriangle(contractInstanceTriangleNew)

      await loadData(contractInstanceTriangleNew)
    }
  });

  function onChangeGasToken(event) {
    selectedGasTokenAmountUpdater(event.target.value)
  }

  async function createNodeTriangle() {
    if (selectedGasTokenAmount <= 0) {
      alert("must send a value greater than 0")
      return
    }
    isClaimingUpdater(true)

    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.buyNodeForTriangles(Web3.utils.toWei(selectedGasTokenAmount)).send()
    } catch (e) {
      console.log("@@@ e", e)
    }finally{
      isClaimingUpdater(false)
    }
  }

  async function createNodeCurrency() {
    if (selectedGasTokenAmount <= 0) {
      alert("must send a value greater than 0")
      return
    }
    isClaimingUpdater(true)

    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.buyNodeForCurrency().send({
        value:Web3.utils.toWei(selectedGasTokenAmount)
      })
    } catch (e) {
      console.log("@@@ e", e)
    }finally{
      isClaimingUpdater(false)
    }
  }

  async function claimRewards(nodeId) {
    isClaimingUpdater(true)
    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.claimRewardsForNode(nodeId).send()
    } catch (e) {
    }
    finally{
      isClaimingUpdater(false)
    }
  }

  async function compoundRewards(nodeId) {
    isClaimingUpdater(true)

    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.compoundNode(nodeId).send()
    } catch (e) {
    }finally{
      isClaimingUpdater(false)
    }
  }

  async function compoundAll() {
    isClaimingUpdater(true)

    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.compoundAllNodes().send()
    } catch (e) {
    }finally{
      isClaimingUpdater(false)
    }
  }

  async function claimAll() {
    isClaimingUpdater(true)

    try {
      let web3 = new Web3(window.ethereum);
      let contractInstanceTriangleNew = await getInstanceTriangle(web3)
      await contractInstanceTriangleNew.methods.claimAllNodes().send()
    } catch (e) {
    }finally{
      isClaimingUpdater(false)
    }
  }

  function timeout(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  async function sleep(fn, ...args) {
    await timeout(1000);
    return fn(...args);
  }

  async function loadData(contractInstanceTriangle) {
    if (window.user != null) {
      let web3 = new Web3(window.ethereum);
      let userNodesCallArray = []
      let stakesBonusCallsArray = []
      let stakesRewardsCallsArray = []
      let localPrice =0

      const [userCurrencyBalance, activeStakes, localUserStakeCounter, gasTokenBalance, trianglePriceInGasToken, ] = await Promise.all([
        contractInstanceTriangle.methods.balanceOf(window.user).call(),
        contractInstanceTriangle.methods.activeStakes().call(),
        contractInstanceTriangle.methods.addressNodeCounter(window.user).call(),
        web3.eth.getBalance(window.user),
        contractInstanceTriangle.methods.getOneTrianglePrice().call()
      ])
      try{
        if(gasTokenPrice == 0){
          const gasTokenPrice = await  axios.get('https://api.coingecko.com/api/v3/simple/price?ids=avalanche-2&vs_currencies=usd',{ crossdomain: true })
          localPrice = gasTokenPrice.data["avalanche-2"].usd
          gasTokenPriceUpdater(parseFloat(localPrice))
        }
      
      }catch(e){
        localPrice = 0
        gasTokenPriceUpdater(0)
      }
      let localTokenPrice = parseFloat(Web3.utils.fromWei(trianglePriceInGasToken + "", 'ether'))
      trianglePriceInGasTokenUpdater(parseFloat(Web3.utils.fromWei(trianglePriceInGasToken + "", 'ether')))

      for (let counter = 0; counter < localUserStakeCounter; ++counter) {
        userNodesCallArray.push(contractInstanceTriangle.methods.addressToNodes(window.user, counter).call())
      }

      const localUserStakes = await Promise.all(userNodesCallArray)
      // console.log("@@@ localUserStakes",localUserStakes)
     


      for (let counter = 0; counter < localUserStakes.length; ++counter) {
        // stakesBonusCallsArray.push(contractInstanceTriangle.methods.calculateBonus(localUserStakes[counter].id).call())
        stakesRewardsCallsArray.push(contractInstanceTriangle.methods.calculateRewardsForNode(localUserStakes[counter].id).call())
      }

      // const stakesBonus = await Promise.all(stakesBonusCallsArray)
      const stakesRewards = await Promise.all(stakesRewardsCallsArray)

      let localUserStakeItems = []
      let now = Date.now()
      now = now / 1000
      // console.log("@@@ now",now)
      let totalRewards =0
      let dailyRewards =0
      for (let counter = 0; counter < localUserStakes.length; ++counter) {
        if (localUserStakes[counter].owner == window.user ) {
          localUserStakeItems.push(
            <div key={localUserStakes[counter].id} className="relative flex flex-col items-center p-6 dropblur infoDiv node" data-aos="zoom-y-out" data-aos-delay="150">
              <div style={{ width: "100%" }}>
                <img src={triangleNode} style={{height:"55px",position:"absolute",top:20,right:25}}/>
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left",width:"100%" }}>Identifier <br /> <span style={{fontWeight:500}}>{localUserStakes[counter].id}</span></h4> <br />
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Triangles in the node  <br />
                <span style={{fontWeight:500}}>{parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether'))}<img className='triangleLogo small' src={triangleLogo} />  
                  <br />
                  $ &nbsp;{parseFloat(parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether'))*parseFloat(localPrice/localTokenPrice/2 )).toFixed(2)}
                </span></h4> <br />
              
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Rewards for claim <br /> <span style={{fontWeight:500}}>{Number.parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether')).toFixed(18)}<img className='triangleLogo small' src={triangleLogo} /> 
                <br />
                  $ &nbsp;{parseFloat(parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether'))*parseFloat(localPrice/localTokenPrice/2 )).toFixed(6)}
                </span></h4> <br />
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left" }}>Rewards for compound  <br /><span style={{fontWeight:500}}>{Number.parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether')).toFixed(18)*101/100}<img className='triangleLogo small' src={triangleLogo} /> 
                <br />
                  $ &nbsp;{parseFloat(parseFloat(Web3.utils.fromWei(stakesRewards[counter]+ "", 'ether')*101/100)*parseFloat(localPrice/localTokenPrice/2 )).toFixed(6)}
                </span></h4> <br />

                {localUserStakes[counter].lastWeekClaim == 0?
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left",marginBottom:"25px" }}>Node APR - 4% / day  <br />  <span style={{fontWeight:500}}> <img className='triangleLogo ' src={triangleLogo} /> {parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether')*0.04).toFixed(9)}  / day
                 <br />
                  $ &nbsp;{parseFloat(parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether')*4/100)*parseFloat(localPrice/localTokenPrice/2 )).toFixed(2)} / day
                </span></h4> 
                :null}
                {localUserStakes[counter].lastWeekClaim == 1?
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left",marginBottom:"25px" }}>Node APR - 3% / day  <br /><span style={{fontWeight:500}}>   <img className='triangleLogo ' src={triangleLogo} /> {parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether')*0.03).toFixed(9)}  / day
                  <br />
                  $ &nbsp;{parseFloat(parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether')*3/100)*parseFloat(localPrice/localTokenPrice/2 )).toFixed(2)} / day
                </span></h4> 
                :null}
                {localUserStakes[counter].lastWeekClaim == 2?
                <h4 className="text-xl font-bold leading-snug tracking-tight mb-1" style={{ float: "left",marginBottom:"25px" }}>Node APR - 1% / day  <br /><span style={{fontWeight:500}}> <img className='triangleLogo ' src={triangleLogo} /> {parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether')*0.01).toFixed(9)}  / day
                  <br />
                  $ &nbsp;{parseFloat(parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether')*1/100)*parseFloat(localPrice/localTokenPrice/2 )).toFixed(2)} / day
                </span></h4> 
                :null}
             
                <br /> <br />
                <br /> <br />
                <br /> <br />
                  <button  className="button-64"  onClick={() => compoundRewards(localUserStakes[counter].id)}>
                <span className="text">Compound rewards</span>
                </button>
                  <br />
                  <button  className="button-64"  onClick={() => claimRewards(localUserStakes[counter].id)}>
                <span className="text">Claim rewards</span>
                </button>
                  <br />
              </div>
            </div>
          )
          totalRewards = parseFloat(totalRewards) + parseFloat(Web3.utils.fromWei(stakesRewards[counter] + "", 'ether'))
          if(localUserStakes[counter].lastWeekClaim == 0){
            dailyRewards = parseFloat(dailyRewards) + parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether')*4/100)
          }else if(localUserStakes[counter].lastWeekClaim == 1){
            dailyRewards = parseFloat(dailyRewards) + parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether')*3/100)
          }else{
            dailyRewards = parseFloat(dailyRewards) + parseFloat(Web3.utils.fromWei(localUserStakes[counter].trianglesInvested + "", 'ether')*1/100)
          }
        }
      }

      


      totalRewardsUpdater(totalRewards)
      dailyRewardsUpdater(dailyRewards)
      userNodeItemsUpdater(localUserStakeItems)
      triangleTokenUpdater(userCurrencyBalance)
      activeStakesUpdater(activeStakes)
      gasTokenBalanceUpdater(gasTokenBalance)

      

    }

    await timeout(1000)
    await loadData(contractInstanceTriangle)

  }



  return (
    <div className="flex flex-col min-h-screen overflow-hidden">
  <div className="overlay"></div>
<div className="dynamicbg" style={{height:"100vh"}}>
  {isClaiming?
<svg  viewBox="0 0 100 115" preserveAspectRatio="xMinYMin slice">
<polygon points="" fill="none" stroke="orange" strokeWidth="10.5">
      <animate attributeName="points" repeatCount="indefinite" dur="8s" begin="0s" from="50 57.5, 50 57.5, 50 57.5" to="50 -95, 175 100, -95 126"></animate>
    </polygon>

  </svg>
  :
  <svg viewBox="0 0 100 115" preserveAspectRatio="xMinYMin slice">
<polygon points="" fill="none" stroke="#4ecfc6" strokeWidth="10.5">
      <animate attributeName="points" repeatCount="indefinite" dur="8s" begin="0s" from="50 57.5, 50 57.5, 50 57.5" to="50 -95, 175 100, -95 126"></animate>
    </polygon>

  </svg>

    }
 
</div>
      {/*  Site header */}
      <Header hasEthereum={hasEthereum} />

      {/*  Page content */}
      <main className="flex-grow">

        {/*  Page sections */}

        {/* <WalletBlocks bears={userNFTs}/> */}

        <section className="relative">
          {/* Section background (needs .relative class on parent and next sibling elements) */}
          <div className="absolute inset-0 top-1  md:mt-24 lg:mt-0 bg-gray-900 pointer-events-none" aria-hidden="true"></div>
          {/* <div className="absolute left-0 right-0 bottom-0 m-auto w-px p-px h-20 bg-gray-200 transform translate-y-1 "></div> */}

          <div className="relative max-w-6xl mx-auto px-4 sm:px-6">
            <div className="py-12 md:py-20">
              <br />
              <br />
              <br />
              {/* Section header */}
              <div className="max-w-2md mx-auto text-center  dropblur infoDiv node">
              <br />

                <h2 className="h2 mb-4">Nodes&nbsp;&nbsp;<img src={triangleNode} className='triangleLogo' style={{height:"65px",transform:"translatey(-10px)"}}/></h2>
                <p className="text-xl text-gray-600 showOnMobileBlock">Create and manage your  nodes.</p>
                <p className="text-xl text-gray-600 hideOnMobileBlock">Create and manage your Triangle <img className='triangleLogo' src={triangleLogo} />  nodes.</p>
                
                <br />

              </div>
              <br />
              <br />
              {/* Items */}
              <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-4 items-start md:max-w-2xl lg:max-w-none">

                <p className="text-xl text-gray-600 dropblur infoDiv node" style={{ textAlign: "center" }}>
                  <b>My Triangle balance</b> <br /> <img className='triangleLogo' src={triangleLogo} /> {Number.parseFloat(Web3.utils.fromWei(triangleToken + "", "ether")).toFixed(15)}
                  <br /> ${(parseFloat(Web3.utils.fromWei(triangleToken + "", "ether"))*parseFloat( gasTokenPrice/trianglePriceInGasToken/2 )).toFixed(2)}</p>
                <p className="text-xl text-gray-600 dropblur infoDiv node" style={{ textAlign: "center" }}><b>My AVAX balance</b> <br />{Number.parseFloat(Web3.utils.fromWei(gasTokenBalance + "", "ether")).toFixed(5)} <br /> ${(parseFloat(Web3.utils.fromWei(gasTokenBalance + "", "ether"))*parseFloat(gasTokenPrice)).toFixed(2)}</p>
                
                <p className="text-xl text-gray-600 dropblur infoDiv node" style={{ textAlign: "center" }}><b>Current rewards</b><br /> <img className='triangleLogo' src={triangleLogo} /> {totalRewards.toFixed(15)}<br /> ${parseFloat(totalRewards*parseFloat( gasTokenPrice/trianglePriceInGasToken/2 )).toFixed(2)}</p>
                <p className="text-xl text-gray-600 dropblur infoDiv node" style={{ textAlign: "center" }}><b>Est. daily rewards</b> <br /> <img className='triangleLogo' src={triangleLogo} /> {dailyRewards.toFixed(15)}<br /> ${parseFloat(dailyRewards*parseFloat( gasTokenPrice/trianglePriceInGasToken/2 )).toFixed(2)}</p>

              </div>
              <br />
              <br />
              <p className="max-w-2md  text-gray-600 dropblur infoDiv node" style={{ textAlign: "center" }}>
                <input onChange={onChangeGasToken} style={{minWidth:"380px"}}  placeholder='Amount (Triangle or AVAX) for node creation' type="number" />
                </p>
                <br />
              <br />
              <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-4 items-start md:max-w-2xl lg:max-w-none">

                <p className="text-xl text-gray-600 dropblur infoDiv node" style={{ textAlign: "center" }}>
                  <button  className="button-64"  onClick={createNodeTriangle}>
                    <span className="text">Create node with <img className='triangleLogo' src={triangleLogo} /> </span>
                  </button>
                </p>
                <p className="text-xl text-gray-600 dropblur infoDiv node" style={{ textAlign: "center" }}>
                  <button  className="button-64"  onClick={createNodeCurrency}>
                  <span className="text"> Create node with AVAX</span>
                  </button>
                </p>

                <p className="text-xl text-gray-600 dropblur infoDiv node" style={{ textAlign: "center" }}>
                  <button  className="button-64"  onClick={compoundAll}>
                  <span className="text"> Compound all nodes</span>
                  </button>
                  
                </p>
                <p className="text-xl text-gray-600 dropblur infoDiv node" style={{ textAlign: "center" }}>
                <button  className="button-64"  onClick={claimAll}>
                <span className="text">Claim from all nodes</span>
                </button>
                </p>

              

              </div>
              <br />
              <br />

            
             

              <div className="max-w-sm mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-3 items-start md:max-w-2xl lg:max-w-none">

                {userNodeItems}

              </div>


            </div>
          </div>
        </section>

      </main>

      {/*  Site footer */}
      <Footer />

    </div>
  );
}

export default NodePage;