import React from 'react';

import Header from '../partials/Header';
import Newsletter from '../partials/Newsletter';
import Footer from '../partials/Footer';

function Home() {
  let hasEthereum = window.ethereum != undefined

  return (
    <div className="flex flex-col min-h-screen overflow-hidden">
  <div className="overlay"></div>
<div className="dynamicbg" style={{height:"100vh"}}>
  <svg viewBox="0 0 100 115" preserveAspectRatio="xMinYMin slice">
     <polygon points="" fill="none" stroke="#4ecfc6" strokeWidth="10.5">
      <animate attributeName="points" repeatCount="indefinite" dur="8s" begin="0s" from="50 57.5, 50 57.5, 50 57.5" to="50 -95, 175 100, -95 126"></animate>
    </polygon>
    <polygon points="" fill="none" stroke=" #3da5e3 " strokeWidth="10.5">
      <animate attributeName="points" repeatCount="indefinite" dur="8s" begin="4s" from="50 57.5, 50 57.5, 50 57.5" to="50 -95, 175 100, -95 126"></animate>
    </polygon>
    
  </svg>
</div>
      {/*  Site header */}
      <Header  hasEthereum={hasEthereum} />

      {/*  Page content */}
      <main className="flex-grow">
        <br />
        <br />
        <br />
        <br />
        <br />
      <div class="containerp">
        <div class="sidep leftp"></div>
        <div class="sidep frontp"></div>
        <div class="sidep rightp"></div>
        <div class="sidep backp"></div>
      </div>
        {/*  Page sections */}
        {/* <HeroHome  hasEthereum={hasEthereum}/> */}
        {/* <FeaturesHome /> */}
        {/* <FeaturesBlocks /> */}
        {/* <Testimonials /> */}
        {/* <Newsletter /> */}

      </main>

      {/*  Site footer */}
      <Footer />
    

    </div>
  );
}

export default Home;